import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link } from 'react-router-dom';
import apiServices,{BASE_URL_Image} from '../../services/apiServices';
export default function ViewTeacher() {
  const [teacher,setTeacher]=useState()
  const [loading,setLoading]=useState(true)
  const override={
		"display":"block",
		"margin":"3rem auto",
		}
  useEffect(()=>{
    apiServices.allTeacher().then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            setTeacher(data.data.Teacher)
        }
        else{
            toast.error(data.data.Message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
const changeStatus=(id,status,qualification,email,password,salary,gender,name,city,phone)=>{
  setLoading(true)
  if(status==true){
   var upstatus=false
  }
  else{
   var upstatus=true
  }
  let data={
    "_id":id, 
    "status":upstatus,
    "teacher_name":name,
    "salary":salary,
    "password":password,
    "city":city,
    "phone_no":phone,
    "email":email,
    "qualification":qualification,
    "gender":gender,
  }
  console.log(data)
  apiServices.updateTeacher(data).then((data)=>{
    // console.log(data)
    setTimeout(()=>{
        setLoading(false)
    },1000)
    if(data.data.success){
        toast.success(data.data.message)
    }
    else{
        toast.error(data.data.message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong!!Try Again Later")
    setTimeout(()=>{
        setLoading(false)
    },1500)
})
}
  return (
    <>
     <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Teachers</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/admin"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Teachers
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full":""}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
            <div className="breadcrumbs " >
          <div className="container pt-2">
            <h2 className="text-center">Manage Teacher</h2>
                </div>
              </div>
                {/* <!-- End Breadcrumbs --> */}
                <div className='container'>
                  <div className='row'>
                    <div className='col-md-10 mx-auto my-3 table-responsive'>
                      <table className='table table-bordered'>
                        <thead className="thead-dark">
                          <tr>
                            <th>SNo</th>
                            <th>Name</th>
                            <th>Qualification</th>
                            <th>Email</th>
                            {/* <th>Password</th> */}
                            <th>City</th>
                            <th>Contact</th>
                            <th>Salary</th>
                            <th>Gender</th>
                            <th>Status</th>
                            <th>Actions</th>
                          </tr>
                        </thead>
                        <tbody>
                          {teacher?.map((data,i)=>(
                            <tr key={i}>
                              <td>{i+1}</td>
                              <td>{data?.teacher_name}</td>
                              <td>{data?.qualification}</td>
                              <td>{data?.email}</td>
                              {/* <td>{data?.password}</td> */}
                              <td>{data?.city}</td>
                              <td>{data?.phone_no}</td>
                              <td>&#8377; {data?.salary}</td>
                              <td>{data?.gender}</td>
                              {data?.status?<td>Active</td>:<td>In-active</td>}
                              <td style={{width:"300px"}}>
                                <Link to={"/admin/update_teacher/"+`${data?._id}`}>
                                <button type="submit" className='btn btn-outline-primary mx-2'>Update</button>
                                </Link>
                                &nbsp;&nbsp;
                                <button type="submit" className='btn btn-outline-danger mx-2' onClick={()=>{changeStatus(data?._id,data?.status,data?.qualification,data?.email,data?.password,data?.salary,data?.gender,data?.teacher_name,data?.city,data?.phone_no)}}>Change Status</button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
          </>
  )
}
