import React, { useEffect, useState } from 'react';
import { toast } from "react-toastify";
import { ClipLoader } from "react-spinners";
import { Link } from 'react-router-dom';
import Moment from 'react-moment';
import apiServices from '../../services/apiServices';

export default function AddVideo() {
    const [loading, setLoading] = useState(true);
    const [courses, setCourses] = useState([]);
    const [courseId, setCourseId] = useState('');
    const [batches, setBatches] = useState([]);
    const [batchId, setBatchId] = useState('');
    const [desc, setDesc] = useState('');
    const [title, setTitle] = useState('');
    const [video, setVideo] = useState(null);
    const [videoName, setVideoName] = useState("");
    
    const override = {
        display: "block",
        margin: "3rem auto",
    };

    useEffect(() => {
        const data1 = { teacher_id: sessionStorage.getItem("teacher_id") };

        apiServices.allBranch(data1).then((data) => {
            setLoading(false);
            if (data.data.Success) {
                setCourses(data.data.Teacher);
            } else {
                toast.error(data.data.Message);
            }
        });

        apiServices.allBatch().then((data) => {
            setLoading(false);
            if (data.data.Success) {
                setBatches(data.data.Teacher);
            } else {
                toast.error(data.data.Message);
            }
        });
    }, []);

    useEffect(() => {
        const data = {
            teacher_id: sessionStorage.getItem("teacher_id"),
            course_id: courseId || undefined,
        };

        apiServices.allBatch(data).then((data) => {
            setLoading(false);
            if (data.data.Success) {
                setBatches(data.data.Teacher);
            } else {
                toast.error(data.data.Message);
            }
        });
    }, [courseId]);

    const save = (e) => {
        e.preventDefault();
        setLoading(true);
        
        let data = new FormData();
        data.append("course_id", courseId);
        data.append("batch_id", batchId);
        data.append("title", title);
        data.append("description", desc);
        data.append("upload_file", video); // Change to upload_file for video
        data.append("teacher_id", sessionStorage.getItem("teacher_id"));

        apiServices.addVideo(data)
        .then((data) => {
            setLoading(false);
            if (data.data.Success) {
                toast.success(data.data.Message);
                // Reset form fields
                setCourseId('');
                setBatchId('');
                setVideo(null);
                setVideoName('');
                setDesc('');
                setTitle('');
            } else {
                toast.error(data.data.Message);
            }
        }).catch((error) => {
            toast.error("Something went wrong");
            setLoading(false);
        });
    };

    return (
        <>
            <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                    <div className="row justify-content-center">
                        <div className="col-lg-10 text-center">
                            <h1 className="display-3 text-white animated slideInDown">Add Video</h1>
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb justify-content-center">
                                    <li className="breadcrumb-item">
                                        <Link className="text-white" to={"/teacher"}>
                                            Home
                                        </Link>
                                    </li>
                                    <li className="breadcrumb-item text-white active" aria-current="page">
                                        Add Video
                                    </li>
                                </ol>
                            </nav>
                        </div>
                    </div>
                </div>
            </div>
            <div className="d-flex justify-content-center">
                <ClipLoader loading={loading} cssOverride={override} size={120} />
            </div>
            <div className={loading ? "disabled-screen-full mymainbox" : "mymainbox"}>
                <div className="breadcrumbs">
                    <div className="container pt-2">
                        <h2 className='text-capitalize text-center'>Add Video</h2>
                    </div>
                </div>
                <div className='container'>
                    <div className='row'>
                        <div className='col-md-10 mx-auto my-3'>
                            <form action='#'>
                                <div className="form-group">
                                    <label htmlFor="name" className='form-label fs-4'>Course Name</label>
                                    <select className='form-control' onChange={(e) => { setCourseId(e.target.value); }} value={courseId}>
                                        <option selected disabled value="">Choose Course</option>
                                        {courses?.map((el, index) => (
                                            <option key={index} value={el?._id}>{el?.course_name}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="name" className='form-label fs-4'>Batch</label>
                                    <select className='form-control' onChange={(e) => { setBatchId(e.target.value); }} value={batchId}>
                                        <option selected disabled value="">Choose Batch</option>
                                        {batches?.map((el, index) => (
                                            <option key={index} value={el?._id}>
                                                <Moment format="YYYY/MM/DD">{el?.starting_date}</Moment>-{el?.time}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="name" className='form-label fs-4'>Title</label>
                                    <input type='text' className='form-control' value={title} onChange={(e) => { setTitle(e.target.value); }} />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="name" className='form-label fs-4'>Upload Video</label>
                                    <input type='text' className='form-control' onChange={(e) => { setVideo(e.target.value); setVideoName(e.target.value); }} />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="name" className='form-label fs-4'>Description</label>
                                    <textarea className='form-control' value={desc} onChange={(e) => { setDesc(e.target.value); }} />
                                </div>
                                <button type="submit" className="btn btn-primary my-3 col-4 offset-md-4 btn-lg" onClick={save}>ADD VIDEO</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
