import React, { useEffect, useState } from 'react';
import { toast } from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate } from 'react-router-dom';
import Moment from 'react-moment';
import apiServices, { BASE_URL_Image } from '../../services/apiServices';

export default function ManageVideo() {
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  
  const override = {
    display: "block",
    margin: "3rem auto",
  };

  useEffect(() => {
    const fetchVideos = async () => {
      const data = { teacher_id: sessionStorage.getItem("teacher_id") };
      try {
        const response = await apiServices.allVideos(data); // Change to your method for fetching videos
        setTimeout(() => {
          setLoading(false);
        }, 1500);
        if (response.data.Success) {
          setVideos(response.data.Videos); // Adjusted to fetch videos
        } else {
          toast.error(response.data.Message);
        }
      } catch (error) {
        toast.error("Something went wrong! Try Again Later");
        setTimeout(() => {
          setLoading(false);
        }, 1000);
      }
    };

    fetchVideos();
  }, [loading]);

  const deleteVideo = (id) => {
    setLoading(true);
    
    const data = { _id: id };
    apiServices.deleteVideo(data).then((response) => {
      setTimeout(() => {
        setLoading(false);
      }, 1000);
      if (response.data.Success) {
        toast.success(response.data.Message);
        setVideos(videos.filter(video => video._id !== id)); // Remove the deleted video from the state
      } else {
        toast.error(response.data.Message);
      }
    }).catch((error) => {
      toast.error("Something went wrong! Try Again Later");
      setTimeout(() => {
        setLoading(false);
      }, 1500);
    });
  };

  return (
    <>
      <div className="container-fluid bg-primary py-5 mb-5 page-header">
        <div className="container py-5">
          <div className="row justify-content-center">
            <div className="col-lg-10 text-center">
              <h1 className="display-3 text-white animated slideInDown">Manage Videos</h1>
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                  <li className="breadcrumb-item">
                    <Link className="text-white" to={"/teacher"}>
                      Home
                    </Link>
                  </li>
                  <li className="breadcrumb-item text-white active" aria-current="page">
                    Manage Videos
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </div>

      <div className="d-flex justify-content-center">
        <ClipLoader loading={loading} cssOverride={override} size={120} />
      </div>

      <div className={loading ? "disabled-screen-full mymainbox" : "mymainbox"}>
        <div className="breadcrumbs">
          <div className="container pt-2">
            <h2 className="text-center">Manage Video Materials</h2>
          </div>
        </div>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12 mx-auto my-2'>
              <div className='container table-responsive'>
                <div className='table-responsive my-5'>
                  <table className='table table-bordered'>
                    <thead className="thead-dark">
                      <tr>
                        <th>SNo</th>
                        <th>Course Name</th>
                        <th>Title</th>
                        <th>Description</th>
                        <th>Upload File</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {videos.map((video, i) => (
                        <tr key={video._id}>
                          <td>{i + 1}</td>
                          <td>{video.course_id?.course_name}</td>
                          <td>{video.title}</td>
                          <td>{video.description}</td>
                          <td>
                            <Link to={BASE_URL_Image + video.upload_file}>
                              <i className='bi bi-eye'></i>
                            </Link>
                          </td>
                          <td>
                            <Link to={"/teacher/update_video/" + `${video._id}`}>
                              <button type="submit" className='btn btn-outline-success mx-2'>
                                <i className='bi bi-pencil-square'></i>
                              </button>
                            </Link>
                            <button type="submit" className='btn btn-outline-danger mx-2' onClick={() => { deleteVideo(video._id) }}>
                              <i className='bi bi-trash'></i>
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
