import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link } from 'react-router-dom';
import apiServices,{BASE_URL_Image} from '../../services/apiServices';
export default function ViewStudent() {
  const [student,setStudent]=useState()
  const [loading,setLoading]=useState(true)
  const override={
    "display":"block",
    "margin":"3rem auto",
 
  }
  useEffect(()=>{
    apiServices.allStudent().then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            setStudent(data.data.Data)
        }
        else{
            toast.error(data.data.Message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
const changeStatus=(id,status,qualification,email,password,rno,gender,name,city,phone)=>{
  setLoading(true)
  if(status==true){
   var upstatus=false
  }
  else{
   var upstatus=true
  }
  let data={
    "_id":id, 
    "status":upstatus,
    "student_name":name,
    "roll_no":rno,
    "password":password,
    "city":city,
    "phone_no":phone,
    "email":email,
    "qualification":qualification,
    "gender":gender,
  }
  // console.log(data)
  apiServices.updateStudent(data).then((data)=>{
    // console.log(data)
    setTimeout(()=>{
        setLoading(false)
    },1000)
    if(data.data.Success){
        toast.success(data.data.Message)
    }
    else{
        toast.error(data.data.Message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong!!Try Again Later")
    setTimeout(()=>{
        setLoading(false)
    },1500)
})
}
  return (
    <>
     <div className="container-fluid bg-primary py-5 mb-5 page-header">
        <div className="container py-5">
        <div className="row justify-content-center">
            <div className="col-lg-10 text-center">
            <h1 className="display-3 text-white animated slideInDown">Students</h1>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                <li className="breadcrumb-item">
                    <Link className="text-white" to={"/admin"}>
                    Home
                    </Link>
                </li>
                <li
                    className="breadcrumb-item text-white active"
                    aria-current="page">
                    Students
                </li>
                </ol>
            </nav>
            </div>
        </div>
        </div>
    </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full":""}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
            <div className="breadcrumbs " >
          <div className="container pt-2">
            <h2 className="text-center">Manage Students</h2>
                </div>
              </div>
                {/* <!-- End Breadcrumbs --> */}
                <div className='container'>
                  <div className='row'>
                    <div className='col-md-10 mx-auto my-5 table-responsive'>
                      <table className='table table-bordered'>
                        <thead className="thead-dark">
                          <tr>
                            <th>SNo</th>
                            <th>Name</th>
                            <th>Qualification</th>
                            <th>Email</th>
                            {/* <th>Password</th> */}
                            <th>City</th>
                            <th>Contact</th>
                            <th>Roll No.</th>
                            <th>Gender</th>
                            <th>Status</th>
                            <th>Assign Batch</th>
                            <th>Actions</th>
                          </tr>
                        </thead>
                        <tbody>
                          {student?.map((data,i)=>(
                            <tr key={i}>
                              <td>{i+1}</td>
                              <td>{data?.student_name}</td>
                              <td>{data?.qualification}</td>
                              <td>{data?.email}</td>
                              {/* <td>{data?.password}</td> */}
                              <td>{data?.city}</td>
                              <td>{data?.phone_no}</td>
                              <td>{data?.roll_no}</td>
                              <td>{data?.gender}</td>
                              {data?.status?<td>Active</td>:<td>In-active</td>}
                              <td className='py-3'>
                                <Link to={"/admin/assign_class/"+data?._id}>
                                <button className='btn btn-sm btn-outline-secondary'>Assign</button>
                                </Link>
                              </td>
                              <td className="py-3" style={{"width":"1000px"}}>
                                <Link to={"/admin/update_student/"+`${data?._id}`}>
                                <button type="submit" className='btn btn-sm btn-outline-primary mx-2'>Update</button>
                                </Link>
                                &nbsp;&nbsp;
                                <button type="submit" className='btn btn-sm btn-outline-danger mx-2' onClick={()=>{changeStatus(data?._id,data?.status,data?.qualification,data?.email,data?.password,data?.roll_no,data?.gender,data?.student_name,data?.city,data?.phone_no)}}>Change Status</button>
                              </td>
                             
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
          </>
  )
}
