import { useEffect } from "react"
import { useState } from "react"
import apiServices from "../../services/apiServices"
import { ClipLoader } from "react-spinners"
import { toast } from "react-toastify"
import { Link } from "react-router-dom"
export default function TeacherDashboard() {
    const [myData,setMyData]=useState()
    const [loading,setLoading]=useState(true)
    const override={
        "display":"block",
        "margin":"3rem auto",
    }
    useEffect(()=>{
        let data={
            teacher_id:sessionStorage.getItem("teacher_id")
        }
        apiServices.allBatch(data).then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setMyData(data.data.Teacher)
                // console.log("data is ",data.data.data)
            }
            else{
                toast.error(data.data.Message)
            }
        })
    },[])
    return (
        <>
           <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Dashboard</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/teacher"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Dashboard
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
           <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}> 
            <div className="container">
                <div className="row" style={{ marginTop: "8rem" }}>
                    <div className="col-md-8 offset-md-2 my-2 p-2 ">
                        {/* <div className="card my-5 p-5"> */}
                            <h1 className="text-center">Welcome {sessionStorage.getItem("user_name")}</h1>
                        {/* </div> */}
                        <h4>Your Batches:</h4>
                    </div>
                    {myData?.map((el,index)=>(
                         <div className="col-md-4 my-3  text-center">
                                <div className="card text-center text-white" style={{backgroundColor: "rgba(0,150,200,0.5)"}}>
                                    <div className="card-body">
                                        {/* <i class='fas fa-user-graduate ' style={{fontSize:"50px"}}></i> */}
                                        <h2 className="">{el?.course_id?.course_name}</h2>
                                        <h4 className="card-text">{el?.time}</h4>
                                    </div>
                                </div>
                     </div>
                    ))}
                </div>
            </div>
        </div>
        </>
    )
}