
import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate, useParams } from 'react-router-dom';
import Moment from 'react-moment';
import Modal from "react-modal";
import apiServices,{BASE_URL_Image} from '../../services/apiServices';
export default function ViewAnswers() {
  const [batch,setBatch]=useState()
  const [loading,setLoading]=useState(true)
  const [isOpen,setIsOpen]=useState(false)
  const [marks,setMarks]=useState()
  const [mmarks,setMmarks]=useState()
  const [remarks,setRemarks]=useState()
  const [aid,setaId]=useState()
  const navigate=useNavigate()
  const override={
		"display":"block",
		"margin":"3rem auto",
		}
  function closeModal() {
    setIsOpen(false);
}
const customStyles = {
content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%,-50%)',
    width:'60%'
},
};
  const {id}=useParams()
  useEffect(()=>{
    let data_id={
        assignmentId:id
    }
    apiServices.allStudentAssignment(data_id).then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.success){
            setBatch(data.data.data)
        }
        else{
            toast.error(data.data.message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
const handleForm=(e)=>{
    e.preventDefault()
    setLoading(true)
    setIsOpen(false)
    let data={
        _id:aid,
        marksObtained:marks,
        remarks:remarks,
        isEvaluated:true
    }
    apiServices.updateStudentAssignment(data).then((data)=>{
     // console.log(data)
     setTimeout(()=>{
         setLoading(false)
     },1500)
     if(data.data.success){
         toast.success(data.data.message)
     }
     else{
         toast.error(data.data.message)
     }
 }).catch((error)=>{
     // console.log(error)
     toast.error("Something went wrong!!Try Again Later")
     setTimeout(()=>{
         setLoading(false)
     },1000)
 })
 }
 const changeStatus=(mm,id)=>{
    console.log(mm)
    setIsOpen(true)
    setMmarks(mm)
    setaId(id)
 }
 useEffect(()=>{
    console.log(mmarks,aid)
 },[mmarks,aid])
  return (
    <>
     <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Answers</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/teacher"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Answers
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
          <div className="breadcrumbs " >
          <div className="container pt-2">
            <h2 className="text-center">Assignments</h2>
                </div>
                </div>
                {/* <!-- End Breadcrumbs --> */}
          <div className='container'>
            <div className='row'>
              <div className='col-md-12 mx-auto my-5' style={{boxShadow:"0px 0px 10px gray"}}>
                <ul className='list-group list-group-flush text-capitalize'>
                    {batch?.map((data,i)=>(
                        <li className='list-group-item pb-3 pt-4'>
                            <div className='row'>
                                <div className='col-md-4'>
                                <h6><i className='bi bi-patch-question-fill pe-3'></i>{data?.assignmentId?.title}</h6>
                                <span>Roll No: {data?.studentId?.roll_no}</span>
                                <h3>
                                    {data?.studentId?.student_name}
                                </h3>
                                </div>
                                <div className='col-md-4'>
                                {!data?.isAttempted?<span className='text-danger fs-5'>Missing</span>:!data?.isEvaluated?<span className='text-danger fs-5'>Not Evaluated</span>:<span className='fs-5'>
                              Marks Obtained- {data?.marksObtained}</span>}
                                </div>
                                <div className='col-md-4'>
                                <span>Due Date <Moment format="YYYY/MM/DD">{data?.batch_id?.assignmentId?.starting_date}</Moment></span>
                                </div>
                            </div>
                           <div className='row'>
                            <div className='col-md-4'>
                                {data?.isAttempted?
                                <Link className='btn btn-outline-primary' to={BASE_URL_Image+data?.studentAnswerFile}>View Answer</Link>
                                :"Not Uploaded"}
                            </div>
                            <div className='col-md-4'>
                                <span >Total Marks {data?.assignmentId?.marks}</span>
                            </div>
                            <div className='col-md-4'>
                            {data?.isAttempted?
                                !data?.isEvaluated?
                                <button className='btn btn-outline-primary' onClick={()=>{changeStatus(data?.assignmentId?.marks, data?._id)}}>Marks</button>:<span className='text-success'>Evaluated</span>:<span className='text-danger'>Missing</span>}
                            </div>
                           </div>
                        </li>
                    ))}
                </ul>       
                  </div>
                </div>
              </div>
            </div>
            <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
        >
        <div className='d-flex justify-content-end'>
            <button onClick={closeModal} className='btn btn-close'></button>
        </div>
        <h3 className='text-center'>Marks Out of {mmarks}</h3>
        <form onSubmit={handleForm}>
            <div className='row my-3'>
                <div className="col-md-2 offset-md-1">Marks</div>
                <div className='col-md-8'>
                    <input type='number' onChange={(e)=>{setMarks(e.target.value)}} value={marks} max={mmarks} min="0" className='form-control' required />
                </div>
            </div> 
            <div className='row my-3'>
                <div className="col-md-2 offset-md-1">Remarks</div>
                <div className='col-md-8'>
                    <input type='text' onChange={(e)=>{setRemarks(e.target.value)}} value={remarks}  className='form-control' required />
                </div>
            </div>  
            <div className='d-flex justify-content-center my-3'>
                <button className='btn btn-success w-25' >Update</button>
            </div>        
        </form>
        </Modal>
          </>
  )
}
