import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import { ClipLoader } from "react-spinners";
import { Link, useNavigate, useParams } from 'react-router-dom';
import Moment from 'react-moment';
import apiServices from '../../services/apiServices';
export default function UpdateMaterial() {
    const [loading,setLoading]=useState(true)
    const [courses,setCourses]=useState()
    const [courseId,setCourseId]=useState()
    const [batches,setBatches]=useState()
    const [batchId,setBatchId]=useState()
    const [desc,setDesc]=useState()
    const [title,setTitle]=useState()
    const [image,setImage]=useState()
    const nav=useNavigate()
    const override={
        "display":"block",
        "margin":"3rem auto",
    }
    const {id}=useParams()
    useEffect(()=>{
        let data={
            _id:id
        }
        apiServices.singleMaterial(data).then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setCourseId(data.data.Material.course_id?._id)
                setBatchId(data.data.Material.batch_id?._id)
                setDesc(data.data.Material.description)
                setImage(data.data.Material.upload_file)
                setTitle(data.data.Material.title)
            }
            else{
                toast.error(data.data.Message)
            }
        })
        let data1={
            teacher_id:sessionStorage.getItem("teacher_id")
        }
        apiServices.allBranch(data1).then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setCourses(data.data.Teacher)
            }
            else{
                toast.error(data.data.Message)
            }
        })
        apiServices.allBatch().then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setBatches(data.data.Teacher)
            }
            else{
                toast.error(data.data.Message)
            }
        })
    },[])
    useEffect(()=>{
        if(courseId=="undefined"||courseId==null||courseId==undefined||courseId==""){
            var data={
                teacher_id:sessionStorage.getItem("teacher_id")
            }
        }
        else{
        var data={
            course_id:courseId,
            teacher_id:sessionStorage.getItem("teacher_id")
        }
    }
        apiServices.allBatch(data).then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setBatches(data.data.Teacher)
            }
            else{
                toast.error(data.data.Message)
            }
        })
    },[courseId])
    const save=(e)=>{
    e.preventDefault()
    setLoading(true)
    let data=new FormData()
    data.append("course_id",courseId)
    data.append("batch_id",batchId)
    data.append("title",title)
    data.append("description",desc)
    data.append("upload_file", image)
    data.append("teacher_id",sessionStorage.getItem("teacher_id"))
    data.append("_id",id)
    apiServices.updateMaterial(data).then((data)=>{
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.success){
            toast.success(data.data.message)
           setTimeout(() => {
            nav("/teacher/view_material")
           }, 1000);
        }
        else{
            toast.error(data.data.message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong")
        setTimeout(()=>{
            setLoading(false)
        },1500)
    })
    }
    return (
        <>
         <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Material</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/teacher"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Material
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}> 
        {/* <!-- ======= Breadcrumbs ======= --> data-aos="fade-in" */}
        <div className="breadcrumbs " >
                    <div className="container pt-2">
                        <h2 className='text-capitalize text-center'>Update Material</h2>
                    </div>
                    </div>
                    {/* <!-- End Breadcrumbs --> */}
            <div className='container'>
            <div className='row'>
                <div className='col-md-10 mx-auto my-3'>
                <form action='#'>
                <div className="form-group">
                    <label htmlFor="name" className='form-label fs-4'>Course Name</label>
                    <select className='form-control' onChange={(e)=>{setCourseId(e.target.value)}} value={courseId}>
                        <option  disabled value="">Choose Course</option>
                        {courses?.map((el,index)=>(
                            <option value={el?._id} selected={courseId==el?._id}>{el?.course_name}</option>
                        ))}
                    </select>
                </div>
                <div className="form-group">
                    <label htmlFor="name" className='form-label fs-4'>Batch</label>
                    <select className='form-control' onChange={(e)=>{setBatchId(e.target.value)}} value={batchId}>
                        <option  disabled value="">Choose Batch</option>
                        {batches?.map((el,index)=>(
                            <option value={el?._id} selected={batchId==el?._id}><Moment format="YYYY/MM/DD">{el?.starting_date}</Moment>-{el?.time}</option>
                        ))}
                    </select>
                </div>
                <div className="form-group">
                    <label htmlFor="name" className='form-label fs-4'>Title</label>
                    <input type='text' className='form-control' value={title} onChange={(e)=>{setTitle(e.target.value)}}/> 
                </div>
                <div className="form-group">
                    <label htmlFor="name" className='form-label fs-4'>Upload File</label>
                    <input type='file' className='form-control' onChange={(e)=>{setImage(e.target.files[0])}}/> 
                </div>
                <div className="form-group">
                    <label htmlFor="name" className='form-label fs-4'>Description</label>
                    <textarea className='form-control' value={desc} onChange={(e)=>{setDesc(e.target.value)}}/> 
                </div>
                <button type="submit" className="btn btn-primary my-3 col-4 offset-md-4 btn-lg" onClick={save}>Update</button>
                </form>
                </div>
            </div>
            </div>
        </div>
        </>
    )
}
