import { Link } from "react-router-dom";
import { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import apiServices from "../services/apiServices"
import { toast } from "react-toastify";
export default function About1() {
    const [branch,setBranch]=useState()
    const [loading,setLoading]=useState(true)
    const override={
          "display":"block",
          "margin":"3rem auto",
          }
    useEffect(()=>{
      apiServices.allBranch().then((data)=>{
          // console.log(data)
          setTimeout(()=>{
              setLoading(false)
          },1500)
          if(data.data.Success){
              setBranch(data.data.Teacher)
          }
          else{
              toast.error(data.data.Message)
          }
      }).catch((error)=>{
          // console.log(error)
          toast.error("Something went wrong!!Try Again Later")
          setTimeout(()=>{
              setLoading(false)
          },1000)
      })
  },[loading])
    return (
        <>
            <div className="container-fluid bg-primary py-5 mb-5 page-header">
        <div className="container py-5">
        <div className="row justify-content-center">
            <div className="col-lg-10 text-center">
            <h1 className="display-3 text-white animated slideInDown">About Us</h1>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                <li className="breadcrumb-item">
                    <Link className="text-white" to={"/"}>
                    Home
                    </Link>
                </li>
                <li
                    className="breadcrumb-item text-white active"
                    aria-current="page">
                    About
                </li>
                </ol>
            </nav>
            </div>
        </div>
        </div>
    </div>
         {/* Service Start */}
  <div className="container-xxl py-5">
    <div className="container">
      <div className="row g-4">
        <div className="col-lg-3 col-sm-6 wow fadeInUp" data-wow-delay="0.1s">
          <div className="service-item text-center pt-3">
            <div className="p-4">
              <i className="fa fa-3x fa-graduation-cap text-primary mb-4" />
              <h5 className="mb-3">Skilled Instructors</h5>
              <p>
                All the teachers are skilled and experienced
              </p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-sm-6 wow fadeInUp" data-wow-delay="0.3s">
          <div className="service-item text-center pt-3">
            <div className="p-4">
              <i className="fa fa-3x fa-globe text-primary mb-4" />
              <h5 className="mb-3">Online Classes</h5>
              <p>
                Get online sessions and upload task
              </p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-sm-6 wow fadeInUp" data-wow-delay="0.5s">
          <div className="service-item text-center pt-3">
            <div className="p-4">
              <i className="fa fa-3x fa-home text-primary mb-4" />
              <h5 className="mb-3">Home Projects</h5>
              <p>
                Assignment to do from home
              </p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-sm-6 wow fadeInUp" data-wow-delay="0.7s">
          <div className="service-item text-center pt-3">
            <div className="p-4">
              <i className="fa fa-3x fa-book-open text-primary mb-4" />
              <h5 className="mb-3">Book Now</h5>
              <p>
                Book class with us now.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  {/* Service End */}
  {/* About Start */}
  <div className="container-xxl py-5">
    <div className="container">
      <div className="row g-5">
        <div
          className="col-lg-6 wow fadeInUp"
          data-wow-delay="0.1s"
          style={{ minHeight: 400 }}
        >
          <div className="position-relative h-100">
            <img
              className="img-fluid position-absolute w-100 h-100"
              src="/assets/img/about.jpg"
              alt=""
              style={{ objectFit: "cover" }}
            />
          </div>
        </div>
        <div className="col-lg-6 wow fadeInUp" data-wow-delay="0.3s">
          <h6 className="section-title bg-white text-start text-primary pe-3">
            About Us
          </h6>
          <h1 className="mb-4">Welcome to Engg.HUB</h1>
          <p className="mb-4">
            Get all your records here with the marks gained and assignment.
          </p>
          <p className="mb-4">
            Check material anytime anywhere online with Engg.HUB
          </p>
          <div className="row gy-2 gx-4 mb-4">
           
            <div className="col-sm-6">
              <p className="mb-0">
                <i className="fa fa-arrow-right text-primary me-2" />
                Online Classroom
              </p>
            </div>
            <div className="col-sm-6">
              <p className="mb-0">
                <i className="fa fa-arrow-right text-primary me-2" />
                Assignments
              </p>
            </div>
            <div className="col-sm-6">
              <p className="mb-0">
                <i className="fa fa-arrow-right text-primary me-2" />
                Skilled Staff
              </p>
            </div>
            <div className="col-sm-6">
              <p className="mb-0">
                <i className="fa fa-arrow-right text-primary me-2" />
                OnlineStudy Material
              </p>
            </div>
            <div className="col-sm-6">
              <p className="mb-0">
                <i className="fa fa-arrow-right text-primary me-2" />
                Notes
              </p>
            </div>
          </div>
          <a className="btn btn-primary py-3 px-5 mt-2" href="">
            Read More
          </a>
        </div>
      </div>
    </div>
  </div>
  {/* About End */}
  <section id="courses" className="courses">
      <div className="container" data-aos="fade-up">
        <div className="row" data-aos="zoom-in" data-aos-delay="100">
        {branch?.slice(0,6).map((el,index)=>(
          <div className="col-lg-4 my-5 p-3  col-md-6 d-flex align-items-stretch" key={index}>
            <div className="course-item card w-100 p-4">
              {/* <img src="assets/img/course-1.jpg" className="img-fluid" alt="..."/> */}
              <div className="course-content">
                <div className="row   mb-3">
                  <div className='col-md-10 text-start'>
                    <h4 className='card-title text-dark'>{el?.course_name}</h4>
                    <p className="card-text text-secondary" >{el?.duration}</p>
                  </div>
                  {/* <div className='col-md'>
                    <span className='fs-1 text-info'>{">"}</span>
                  </div> */}
                </div>
                </div>
              </div>
            </div>
        ))}
        </div>
      </div>
    </section>
 
        </>
    );
}