import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate } from 'react-router-dom';
import apiServices from '../services/apiServices';
export default function Courses(){
  const [branch,setBranch]=useState()
  const [loading,setLoading]=useState(true)
  const navigate=useNavigate()
  const override={
		"display":"block",
		"margin":"3rem auto",
		}
  useEffect(()=>{
    apiServices.allBranch().then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            setBranch(data.data.Teacher)
        }
        else{
            toast.error(data.data.Message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
    return(
        <>
        <div className="container-fluid bg-primary py-5 mb-5 page-header">
            <div className="container py-5">
            <div className="row justify-content-center">
                <div className="col-lg-10 text-center">
                <h1 className="display-3 text-white animated slideInDown">
                    Courses
                </h1>
                <nav aria-label="breadcrumb">
                    <ol className="breadcrumb justify-content-center">
                    <li className="breadcrumb-item">
                        <Link to={"/"} className="text-white" href="#">
                        Home 
                        </Link>
                    </li>
                    
                    <li
                        className="breadcrumb-item text-white active"
                        aria-current="page"
                    >
                        Courses
                    </li>
                    </ol>
                </nav>
                </div>
            </div>
            </div>
        </div>
         <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={60}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
         <section id="courses" className="courses">
      <div className="container" data-aos="fade-up">
        <div className="row" data-aos="zoom-in" data-aos-delay="100">
        {branch?.map((el,index)=>(
          <div className="col-lg-4 my-5 p-3  col-md-6 d-flex align-items-stretch" key={index}>
            <div className="course-item card w-100 p-4">
              {/* <img src="assets/img/course-1.jpg" className="img-fluid" alt="..."/> */}
              <div className="course-content">
                <div className="row   mb-3">
                  <div className='col-md-10 text-start'>
                    <h4 className='card-title text-dark'>{el?.course_name}</h4>
                    <p className="card-text text-secondary" >{el?.duration}</p>
                  </div>
                  {/* <div className='col-md'>
                    <span className='fs-1 text-info'>{">"}</span>
                  </div> */}
                </div>
                </div>
              </div>
            </div>
        ))}
        </div>
      </div>
    </section>
    </div>
        </>
    );
}