import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate, useParams } from 'react-router-dom';
import Moment from 'react-moment';
import apiServices,{BASE_URL_Image} from '../../services/apiServices';
export default function ViewMarks() {
  const [batch,setBatch]=useState()
  const [loading,setLoading]=useState(true)
  const navigate=useNavigate()
  const override={
      "display":"block",
      "margin":"3rem auto",
      
  }
  const {id}=useParams()
  useEffect(()=>{
    let data_id={
        studentId:sessionStorage.getItem("student_id")
    }
    apiServices.allStudentAssignment(data_id).then((data)=>{
        console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.success){
            setBatch(data.data.data)
        }
        else{
            toast.error(data.data.message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])

  return (
    <>
       <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Assignment</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/student"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Assignment
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
          <div className="breadcrumbs " >
          <div className="container pt-2">
            <h2 className="text-center">Student's Answer</h2>
                </div>
                </div>
                {/* <!-- End Breadcrumbs --> */}
          <div className='container'>
            <div className='row'>
              <div className='col-md-12 mx-auto my-2' style={{boxShadow:"0px 0px 10px gray"}}>
                <ul className='list-group list-group-flush text-capitalize'>
                    {batch?.map((data,i)=>(
                        <li className='list-group-item pb-3 pt-4'>
                            <div className='row'>
                                <div className='col-md-4'>
                                    <h3><i className='bi bi-patch-question-fill pe-3'></i>{data?.assignmentId?.title}</h3>
                                </div>
                                <div className='col-md-4'>
                                {!data?.isAttempted?<span className='text-danger fs-5'>Missing</span>:!data?.isEvaluated?<span className='text-danger fs-5'>Not Evaluated</span>:<span className='fs-5'>
                                Marks Obtain- {data?.marksObtained}</span>}
                                </div>
                                <div className='col-md-4'>
                                <span>Due Date <Moment format="YYYY/MM/DD">{data?.batch_id?.assignmentId?.starting_date}</Moment></span>
                                </div>
                            </div>
                            <div className='row'>
                            {/* <div className='col-md-4'>
                                {data?.isAttempted?
                                <Link className='btn btn-outline-primary' to={BASE_URL_Image+data?.studentAnswerFile}>View Answer</Link>
                                :"Not Uploaded"}
                            </div> */}
                                <div className='col-md-4'>
                                {data?.isAttempted?
                                    <Link className='btn btn-outline-primary' to={BASE_URL_Image+data?.studentAnswerFile}>View Answer</Link>
                                    :"Not Uploaded"}
                                </div>
                                <div className='col-md-4'>
                                <span>Total Marks {data?.assignmentId?.marks}</span>
                                </div>
                                <div className='col-md-4'>
                                <Link to={"/student/view_single_assignment/"+data?._id} className='btn btn-outline-primary'>View Details</Link>
                                </div>
                            </div>
                        </li>
                    ))}
                </ul>       
                  </div>
                </div>
              </div>
            </div>
          </>
  )
}