import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate, useParams } from 'react-router-dom';
import Moment from 'react-moment';
import Modal from "react-modal";
import apiServices,{BASE_URL_Image} from '../../services/apiServices';
export default function ViewSingleAssignment() {
  const [batch,setBatch]=useState()
  const [loading,setLoading]=useState(true)
  const [isOpen,setIsOpen]=useState(false)
  const [ans,setAns]=useState()
  const navigate=useNavigate()
  const override={
		"display":"block",
		"margin":"3rem auto",
		}
  function closeModal() {
    setIsOpen(false);
}
const customStyles = {
content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%,-50%)',
    width:'60%'
},
};

  const {id}=useParams()
  useEffect(()=>{
    let data_id={
        _id:id
    }
    apiServices.singleStudentAssignment(data_id).then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.success){
            setBatch(data.data.data)
        }
        else{
            toast.error(data.data.message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
const handleForm=(e)=>{
   e.preventDefault()
   setLoading(true)
   setIsOpen(false)
   let data=new FormData()
   data.append("studentAnswerFile",ans)
   data.append("batchId",batch?.batchId?._id)
   data.append("studentId",sessionStorage.getItem("student_id"))
   data.append("_id",id)
   apiServices.addAnswer(data).then((data)=>{
    // console.log(data)
    setTimeout(()=>{
        setLoading(false)
    },1500)
    if(data.data.success){
        toast.success(data.data.message)
    }
    else{
        toast.error(data.data.message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong!!Try Again Later")
    setTimeout(()=>{
        setLoading(false)
    },1000)
})
}

  return (
    <>
       <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Assignment</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/student"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Assignment
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
          <div className="breadcrumbs " >
          <div className="container pt-2">
            <h2 className="text-center">Assignments</h2>
                </div>
                </div>
                {/* <!-- End Breadcrumbs --> */}
          <div className='container'>
            <div className='row'>
              <div className='col-md-8 offset-md-2 mx-auto my-2'>
                <div className='card p-5'>
                    <div className='d-flex justify-content-between'>
                        <h3><i className='bi bi-patch-question-fill pe-3'></i>{batch?.assignmentId?.title}</h3>
                        <span>Due Date <Moment format="YYYY/MM/DD">{batch?.batchId?.starting_date}</Moment></span>
                    </div>
                    <div className='text-muted'>{batch?.description}</div>
                    <div className='d-flex justify-content-between'>
                        <span>Marks: {batch?.assignmentId?.marks}</span>
                        <span>Remarks: {batch?.remarks}</span>
                        {!batch?.isAttempted?<span className='text-danger'>Missing<br/> <button className='btn btn-outline-primary' onClick={()=>{setIsOpen(true)}}>Upload</button></span>:!batch?.isEvaluated?<span className='text-primary'>Not Evaluated</span>:<>Marks Obtained: {batch?.marksObtained}</>}
                    </div>
                  </div>
                </div>
                </div>
              </div>
            </div>
        <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
        >
        <div className='d-flex justify-content-end'>
            <button onClick={closeModal} className='btn btn-close'></button>
        </div>
        <h3 className='text-center'>Add Answer</h3>
        <form onSubmit={handleForm}>
            <div className='row my-3'>
                <div className="col-md-2 offset-md-1">Upload File</div>
                <div className='col-md-8'>
                    <input type='file' onChange={(e)=>{setAns(e.target.files[0])}} className='form-control' required />
                </div>
            </div>  
           
            <div className='d-flex justify-content-center my-3'>
                <button className='btn btn-success w-25' >Update</button>
            </div>        
        </form>
        </Modal>
          </>
  )
}
