import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import apiServices from "../services/apiServices";
import { toast } from "react-toastify";

export default function Dashboard() {
      const [data,setData]=useState({})
    useEffect(()=>{
        apiServices.dashboard().then((res)=>{
            setData(res.data)
        }).catch((err)=>{
            toast.error("Something went Wrong.Try again later!")
        })
    },[])
    return (
        <>
        <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Dashboard</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/admin"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Dashboard
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
            <div className="container">
                    <h1 className="text-center">Welcome Admin</h1>
                    <div className="row">
                        <div className="col-md-6 my-5  ">
                            <div className="card text-center text-white" style={{backgroundColor:"rgb(0, 167, 185)"}}>
                                <div className="card-body">
                                    <i class='fas fa-user-graduate ' style={{fontSize:"50px"}}></i>
                                    <h5 className="card-title text-white mt-3">STUDENTS</h5>
                                    <p className="card-text">{data.totalStudents}</p>

                                </div>
                            </div>
                        </div>
                        <div className="col-md-6 my-5 ">
                            <div className="card text-center text-white" style={{backgroundColor: "rgb(0, 167, 185)"}}>
                                <div className="card-body">
                                    <i class='fas fa-user-alt' style={{fontSize:"50px"}}></i>
                                    <h5 className="card-title text-white mt-3">TEACHERS</h5>
                                    <p className="card-text">{data.totalTeachers}</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6 my-5">
                            <div className="card text-center text-white" style={{backgroundColor: "rgb(0, 167, 185)"}}>
                                <div className="card-body">
                                    <i className="fa fa-plus" style={{fontSize:"50px"}}></i>
                                    <h5 className="card-title text-white mt-3 text-white mt-3">Courses</h5>
                                    <p className="card-text">{data.totalCourses}</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6 my-5">
                            <div className="card text-center text-white" style={{backgroundColor: "rgb(0, 167, 185)"}}>
                                <div className="card-body">
                                    <i class='fas fa-tasks ' style={{fontSize:"50px"}}></i>
                                    <h5 className="card-title text-white mt-3">Batches</h5>
                                    <p className="card-text">{data.totalBatches}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        </>
    )
}