import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate } from 'react-router-dom';
import apiServices from '../../services/apiServices';
import Moment from 'react-moment';
export default function ViewBatch() {
  const [batch,setBatch]=useState()
  const [loading,setLoading]=useState(true)
  const navigate=useNavigate()
  const override={
		"display":"block",
		"margin":"3rem auto",
		}
  useEffect(()=>{
    apiServices.allBatch().then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            setBatch(data.data.Teacher)
        }
        else{
            toast.error(data.data.Message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
const changeStatus=(id,status,course,teacher,time,date)=>{
  setLoading(true)
  if(status==true){
   var upstatus=false
  }
  else{
   var upstatus=true
  }
  let data={
    _id:id, 
    status:upstatus,
    course_id:course,
    teacher_id:teacher,
    time:time,
    starting_date:date
  }
  apiServices.updateBatch(data).then((data)=>{
    // console.log(data)
    setTimeout(()=>{
        setLoading(false)
    },1000)
    if(data.data.success){
        toast.success(data.data.Message)
        setTimeout(()=>{
          window.location.reload()
        },1000)
    }
    else{
        toast.error(data.data.Message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong!!Try Again Later")
    setTimeout(()=>{
        setLoading(false)
    },1500)
})
}
  return (
    <>
      <div className="container-fluid bg-primary py-5 mb-5 page-header">
        <div className="container py-5">
        <div className="row justify-content-center">
            <div className="col-lg-10 text-center">
            <h1 className="display-3 text-white animated slideInDown">Batches</h1>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                <li className="breadcrumb-item">
                    <Link className="text-white" to={"/admin"}>
                    Home
                    </Link>
                </li>
                <li
                    className="breadcrumb-item text-white active"
                    aria-current="page">
                    Batches
                </li>
                </ol>
            </nav>
            </div>
        </div>
        </div>
    </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
          <div className="breadcrumbs " >
          <div className="container pt-2">
            <h2 className="text-center">Manage Batch</h2>
                </div>
                </div>
                {/* <!-- End Breadcrumbs --> */}
          <div className='container'>
            <div className='row'>
              <div className='col-md-12 mx-auto my-3'>
                <div className='container table-repsonsive'>
                  <div className='table-responsive  my-5'>
                    <table className='table table-bordered'>
                      <thead className="thead-dark">
                        <tr>
                          <th>SNo</th>
                          <th>Course Name</th>
                          <th>Teacher Name</th>
                          <th>Starting Date</th>
                          <th>Time</th>
                          <th>Status</th>
                          <th>Actions</th>
                        </tr>
                      </thead>
                      <tbody>
                        {batch?.map((data,i)=>(
                          <tr key={i}>
                            <td>{i+1}</td>
                            <td>{data?.course_id?.course_name}</td>
                            <td>{data?.teacher_id?.teacher_name}</td>
                            <td><Moment format="YYYY/MM/DD">{data?.starting_date}</Moment></td>
                            <td>{data?.time}</td>
                            <td>{data?.status?"Active":"In-active"}</td>
                            <td>
                              <Link to={"/admin/update_batch/"+`${data?._id}`}>
                              <button type="submit" className='btn btn-outline-primary mx-2'>Update</button>
                              </Link>
                              <button type="submit" className='btn btn-outline-danger mx-2' onClick={()=>{changeStatus(data?._id,data?.status,data?.course_id?._id,data?.teacher_id?._id,data?.time,data?.starting_date)}}>Delete</button>
                            </td>
                          </tr>
                        ))}
                        
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          </div>
          </>
  )
}
