import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate } from 'react-router-dom';
import apiServices from '../../services/apiServices';
export default function ViewCourse() {
  const [branch,setBranch]=useState()
  const [loading,setLoading]=useState(true)
  const navigate=useNavigate()
  const override={
		"display":"block",
		"margin":"3rem auto",
		}
  useEffect(()=>{
    apiServices.allBranch().then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            setBranch(data.data.Teacher)
        }
        else{
            toast.error(data.data.Message)
        }
    })
    .catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
const changeStatus=(id,status)=>{
  setLoading(true)
  if(status=="true"){
   var upstatus=false
  }
  else{
   var upstatus=true
  }
  let data={
    _id:id, 
    status:upstatus
  }
  apiServices.updateBranch(data)
  .then((data)=>{
    console.log(data)
    setTimeout(()=>{
        setLoading(false)
    },1000)
    if(data.data.success){
        toast.success(data.data.Message)
        setTimeout(()=>{
          // window.location.reload()
        },1000)
    }
    else{
        toast.error(data.data.Message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong!!Try Again Later")
    setTimeout(()=>{
        setLoading(false)
    },1500)
})
}
  return (
    <>
     <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Courses</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/admin"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Courses
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
          <div className="breadcrumbs" >
                <div className="container pt-2">
                    <h2 className='text-center'>Manage Course</h2>
                </div>
                </div>
                {/* <!-- End Breadcrumbs --> */}
          <div className='container'>
            <div className='row'>
              <div className='col-md-12 mx-auto my-3'>
                <div className='container table-repsonsive'>
                  <div className='table-responsive  my-5'>
                    <table className='table table-bordered'>
                      <thead className="thead-dark">
                        <tr>
                          <th>SNo</th>
                          <th>Course Name</th>
                          <th>Duration</th>
                          <th>Status</th>
                          <th>Actions</th>
                        </tr>
                      </thead>
                      <tbody>
                        {branch?.map((data,i)=>(
                          <tr key={i}>
                            <td>{i+1}</td>
                            <td>{data?.course_name}</td>
                            <td>{data?.duration}</td>
                            <td>{data?.status?"Active":"In-active"}</td>
                            <td>
                              <Link to={"/admin/update_course/"+`${data?._id}`}>
                                   <button type="submit" className='btn btn-outline-success mx-2'>Update</button>
                              </Link>
                              
                              {/* <button type="submit" className='btn btn-outline-danger mx-2' onClick={()=>{changeStatus(data?._id,data?.status)}}>Change Status</button> */}
                            </td>
                          </tr>
                        ))}
                        
                      </tbody>
                      
                    </table>
                    
                  </div>
                </div>
              </div>
            </div>
          </div>
          </div>
          </>
  )
}
