import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import ClipLoader from 'react-spinners/ClipLoader';
import { Link, useNavigate } from 'react-router-dom';
import Moment from 'react-moment';
import apiServices, { BASE_URL_Image } from '../../services/apiServices';
export default function ViewMaterial() {
  const [batch,setBatch]=useState()
  const [loading,setLoading]=useState(true)
  const navigate=useNavigate()
  const override={
		"display":"block",
		"margin":"3rem auto",
		}
  useEffect(()=>{
    let data={
      teacher_id:sessionStorage.getItem("teacher_id")
    }
    apiServices.allMaterial(data).then((data)=>{
        // console.log(data)
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            setBatch(data.data.Material)
        }
        else{
            toast.error(data.data.Message)
        }
    }).catch((error)=>{
        // console.log(error)
        toast.error("Something went wrong!!Try Again Later")
        setTimeout(()=>{
            setLoading(false)
        },1000)
    })
},[loading])
const changeStatus=(id)=>{
  setLoading(true)
  
  let data={
    _id:id, 
  }
  apiServices.deleteMaterial(data).then((data)=>{
    // console.log(data)
    setTimeout(()=>{
        setLoading(false)
    },1000)
    if(data.data.Success){
        toast.success(data.data.Message)
        setTimeout(()=>{
          window.location.reload()
        },1000)
    }
    else{
        toast.error(data.data.Message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong!!Try Again Later")
    setTimeout(()=>{
        setLoading(false)
    },1500)
})
}
  return (
    <>
     <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Material</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/teacher"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Material
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
          <div className="breadcrumbs " >
          <div className="container pt-2">
            <h2 className="text-center">Manage Material</h2>
                </div>
                </div>
                {/* <!-- End Breadcrumbs --> */}
          <div className='container'>
            <div className='row'>
              <div className='col-md-12 mx-auto my-2'>
                <div className='container table-repsonsive'>
                  <div className='table-responsive  my-5'>
                    <table className='table table-bordered'>
                      <thead className="thead-dark">
                        <tr>
                          <th>SNo</th>
                          <th>Course Name</th>
                          <th>Starting Date</th>
                          <th>Time</th>
                         <th>Title</th>
                          <th>Description</th>
                          <th colSpan={3}>Actions</th>
                        </tr>
                      </thead>
                      <tbody>
                        {batch?.map((data,i)=>(
                          <tr key={i}>
                            <td>{i+1}</td>
                            <td>{data?.course_id?.course_name}</td>
                            <td><Moment format="YYYY/MM/DD">{data?.batch_id?.starting_date}</Moment></td>
                            <td>{data?.batch_id?.time}</td>
                            <td>{data?.title}</td>
                            <td>{data?.description}</td>
                            <td>
                            <Link to={BASE_URL_Image+data?.upload_file} ><i className='bi bi-eye'></i></Link>
                            </td>
                            {/* <td>{data?.status?"Active":"In-active"}</td> */}
                            <td>
                              <Link to={"/teacher/update_material/"+`${data?._id}`}>
                              <button type="submit" className='btn btn-outline-success mx-2'><i className='bi bi-pencil-square'></i></button>
                              </Link>
                              <button type="submit" className='btn btn-outline-danger mx-2' onClick={()=>{changeStatus(data?._id)}}><i className='bi bi-trash'></i></button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          </div>
          </>
  )
}
