import React, { useEffect, useState } from 'react'
import {toast, ToastContainer} from "react-toastify";
import { ClipLoader } from "react-spinners";
import { Link, useNavigate, useParams } from 'react-router-dom';
import apiServices from '../../services/apiServices';
export default function UpdateCourse() {
const navigate=useNavigate()
  const [loading,setLoading]=useState(false)
  const [branchName,setName]=useState()
  const [duration,setDuration]=useState()
  const param=useParams()
    const id=param.id
  const name=(e)=>{
    setName(e.target.value)
  }
 const override={
		"display":"block",
		"margin":"3rem auto",
		}
useEffect(()=>{
    setLoading(true)
    let data={
        _id:id
    }
    apiServices.singleBranch(data).then(
        (data)=>{
            // console.log(data.data.data.name)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setName(data.data.Teacher.course_name)
                setDuration(data.data.Teacher.duration)
                setLoading(false)
            }
            else{
                toast.error(data.data.Message)
            }
        }
    ).catch(
        (error)=>{
        toast.error("Something went wrong")
        setTimeout(()=>{
            setLoading(false)
        },1000)
        }
    )
},[])

const save=(e)=>{
   e.preventDefault()
//    setLoading(true)
   let data={
    _id:id,
    course_name:branchName,
    duration:duration
   }
   apiServices.updateBranch(data).then((data)=>{
    // setTimeout(()=>{
    // },1500)
    if(data.data.success){
        toast.success(data.data.Message)
        setTimeout(()=>{
            navigate("/admin/view_course")
            setLoading(false)
        },1500)
    }
    else{
        toast.error(data.data.Message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong")
    setTimeout(()=>{
        setLoading(false)
    },1500)
})
}
  return (
    <>
     <div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Courses</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/admin"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Courses
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
    <div className="d-flex justify-content-center">
        <ClipLoader loading={loading} cssOverride={override} size={120}/>
        <ToastContainer/>
    </div>
    <div className={loading?"disabled-screen-full mymainbox" :"mymainbox"}> 
       {/* <!-- ======= Breadcrumbs ======= --> data-aos="fade-in" */}
            <div className="breadcrumbs " >
                <div className="container pt-2">
                    <h2 className='text-center'>Update Course</h2>
                </div>
            </div>
                {/* <!-- End Breadcrumbs --> */}
        <div className='container'>
            <div className='row'>
                <div className='col-md-10 mx-auto my-3'>
                    <form action='#'>
                    <div className="form-group">
                        <label htmlFor="name" className='form-label fs-4'>Course Name</label>
                        <input type="text" className="form-control form-control-lg" id="name" value={branchName} onChange={name} placeholder="Course Name" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="name" className='form-label fs-4'>Duration</label>
                        <input type="text" className="form-control form-control-lg" id="name" value={duration} onChange={(e)=>{setDuration(e.target.value)}} placeholder="Enter Duration" />
                    </div>
                    <button type="submit" className="btn btn-primary my-3 col-4 offset-md-4 btn-lg" onClick={save}>Update</button>
                    </form>
                </div>
            </div>
        </div>


    </div>
    </>
  )
}
