import { Link, useNavigate } from "react-router-dom";
import { useState,useEffect } from "react";
import { ClipLoader } from "react-spinners";
import { ToastContainer,toast } from "react-toastify";
import apiServices from "../services/apiServices";
import "react-toastify/dist/ReactToastify.css"
export default function Login() {
	const [loading, setLoading]=useState(false)
	const navigate=useNavigate();
	const [email,setEmail]=useState();
	const [password,setPassword]=useState();
	const[message,setMessage]=useState()
	const override={
		"display":"block",
		"margin":"3rem auto",
		}
		useEffect(()=>{
			setMessage(sessionStorage.getItem("message"))
			if(message){
				toast.error(message)
				setTimeout(()=>{
					sessionStorage.removeItem("message")
				},2000)
			}
		},[message])
		const handleForm=(e)=>{
		e.preventDefault()
		setLoading(true)
		let data={
			email:email,
			password:password
		}
		apiServices.login(data).then(
		(data) => {
			setTimeout(()=>{
			setLoading(false)
			},1000)
			// setLoad(false);
			if (data.data.success) {
			toast.success(data.data.message)
			sessionStorage.setItem("user_name", data.data.data.name)
			sessionStorage.setItem("user_email", data.data.data.email)
			sessionStorage.setItem("token", data.data.token)
			sessionStorage.setItem("user_type", data.data.data.userType)
			sessionStorage.setItem("authenticate",true)
			sessionStorage.setItem("user_id",data.data.data._id)
			sessionStorage.setItem("status",data.data.data.status)
			if(data.data.data.userType=='1'||data.data.data.userType==1){
				setTimeout(
					() => {
					navigate('/admin')
					}, 1000
				)
			}
			else if(data.data.data.userType=='3'||data.data.data.userType==3){
				sessionStorage.setItem("teacher_id",data.data.data.teacherId)
				setTimeout(
					() => {
					navigate('/teacher')
					}, 1000
				)
			}
			else{
				sessionStorage.setItem("student_id",data.data.data.studentId)
				setTimeout(
					() => {
					navigate('/student')
					}, 1000
				)
			}
			}
			else{
			toast.error(data.data.message)
			}
	
		}
		).catch(
		(error) => {
			setTimeout(()=>{
			setLoading(false)
			},1000)
			// setLoad(false);
			console.log(error)
			toast.error("Something went wrong!!Try again later!!")
		}
		)
		}
	return (
		<>
		
		<div className="container-fluid bg-primary py-5 mb-5 page-header">
                <div className="container py-5">
                <div className="row justify-content-center">
                    <div className="col-lg-10 text-center">
                    <h1 className="display-3 text-white animated slideInDown">Login</h1>
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb justify-content-center">
                        <li className="breadcrumb-item">
                            <Link className="text-white" to={"/"}>
                            Home
                            </Link>
                        </li>
                        <li
                            className="breadcrumb-item text-white active"
                            aria-current="page">
                            Login
                        </li>
                        </ol>
                    </nav>
                    </div>
                </div>
                </div>
            </div>
			<div className="d-flex justify-content-center">
            	<ClipLoader loading={loading} cssOverride={override} size={60}/>
        	</div>
        	<div className={loading?"disabled-screen-full":""}> 
         
            <div className="container">
            <div className="row g-4 wow fadeInUp" data-wow-delay="0.5s ">
                <form className="shadow p-4 mx-auto" style={{ maxWidth: 550 }} onSubmit={handleForm} method="post">
                    <div className="text-center wow fadeInUp" data-wow-delay="0.1s">
                    <h1 className="mb-5 bg-white text-center px-3">Login</h1>
                    </div>
                    <div className="row g-2">
                    <div className="col-12">
                        <div className="form-floating">
                        <input type="email" className="form-control" id="email" placeholder="Email Address"
                        value={email} onChange={(e)=>{setEmail(e.target.value)}}/>
                        <label htmlFor="email">Email Address</label>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="form-floating">
                        <input type="password" className="form-control" id="password" placeholder="Password"
                        value={password} onChange={(e)=>{setPassword(e.target.value)}}/>
                        <label htmlFor="password">Password</label>
                        </div>
                    </div>
                   
                    <div className="col-12">
                        <button className="btn btn-primary w-100 py-3" type="submit">
                        Login
                        </button>
                    </div>
                    
                    </div>
                </form>
            </div>
            </div>
            {/* Login End */}
			</div>
		
		</>
	)
}