import { useEffect,useState } from "react";
import { Link, Navigate, useNavigate, useParams } from "react-router-dom";
import apiServices from "../../services/apiServices";
import { ClipLoader } from "react-spinners";
import { toast } from "react-toastify";
import Moment from "react-moment";
export default function AssignBatch(){
    const {id}=useParams()
    const nav=useNavigate()
    const [mydata,setMydata]=useState()
    const [student,setStudent]=useState()
    useEffect(()=>{
        let data={
            studentId:id
        }
        apiServices.allAssign(data).then((data)=>{
            // console.log(data.data.data)
        if(data.data.success){
          if(data.data.data=="undefined"||data.data.data==undefined||data.data.data==""||data.data.data==null){
           nav("/admin/add_assign_class/"+id)
          }
          else{
            setMydata(data.data.data)
            setTimeout(()=>{
                setLoading(false)
            },1000)
          }
        }
        })
        let data1={
            _id:id
        }
        apiServices.singleStudent(data1).then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setStudent(data.data.Data)
            }
            else{
                toast.error(data.data.Message)
            }
        })
    },[])
const [loading,setLoading]=useState(true)
  const override={
    "display":"block",
    "margin":"3rem auto",
 
  }
  const changeStatus=(id)=>{
    setLoading(true)
    let data={
      _id:id, 
    }
    apiServices.deleteAssign(data).then((data)=>{
      // console.log(data)
      setTimeout(()=>{
          setLoading(false)
      },1000)
      if(data.data.success){
          toast.success(data.data.message)
          setTimeout(()=>{
            window.location.reload()
          },900)
      }
      else{
          toast.error(data.data.message)
      }
  }).catch((error)=>{
      // console.log(error)
      toast.error("Something went wrong!!Try Again Later")
      setTimeout(()=>{
          setLoading(false)
      },1500)
  })
  }
    return(
        <>
         <div className="container-fluid bg-primary py-5 mb-5 page-header">
        <div className="container py-5">
        <div className="row justify-content-center">
            <div className="col-lg-10 text-center">
            <h1 className="display-3 text-white animated slideInDown">Batch</h1>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                <li className="breadcrumb-item">
                    <Link className="text-white" to={"/admin"}>
                    Home
                    </Link>
                </li>
                <li
                    className="breadcrumb-item text-white active"
                    aria-current="page">
                    Batch
                </li>
                </ol>
            </nav>
            </div>
        </div>
        </div>
    </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={120}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}>  
        {/* <!-- ======= Breadcrumbs ======= --> */}
          <div className="breadcrumbs" >
                <div className="container pt-2">
                   
                    <h2>Manage Assigned Batch to {student?.student_name}</h2>
                </div>
                </div>
                {/* <!-- End Breadcrumbs --> */}
          <div className='container'>
            <div className="d-flex justify-content-end mt-2">
                <Link to={"/admin/add_assign_class/"+id}>
                    <button className="btn btn-primary">Assign New</button>
                </Link>
            </div>
            <div className='row'>
              <div className='col-md-12 mx-auto '>
                <div className='container table-repsonsive'>
                  <div className='table-responsive  my-5'>
                    <table className='table table-bordered'>
                      <thead className="thead-dark">
                        <tr>
                          <th>SNo</th>
                          <th>Course Name</th>
                          <th>Starting Date</th>
                          <th>Time</th>
                          <th>Actions</th>
                        </tr>
                      </thead>
                      <tbody>
                        {mydata?.map((data,i)=>(
                          <tr key={i}>
                            <td>{i+1}</td>
                            <td>{data?.courseId?.course_name}</td>
                            <td><Moment format="YYYY/MM/DD">{data?.batchId?.starting_date}</Moment></td>
                            <td>{data?.batchId?.time}</td>
                            <td>
                              <Link to={"/admin/update_assign_batch/"+`${data?._id}`}>
                              <button type="submit" className='btn btn-outline-primary mx-2'>Update</button>
                              </Link>
                              <button type="submit" className='btn btn-outline-danger mx-2' onClick={()=>{changeStatus(data?._id)}}>Delete</button>
                            </td>
                          </tr>
                        ))}
                        
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          </div>
          </>
    )
}