import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import { ClipLoader } from "react-spinners";
import apiServices from '../../services/apiServices';
import { Link, useNavigate, useParams } from 'react-router-dom';
import Moment from 'react-moment';
import moment from "moment"
export default function UpdateBatch() {
  const [loading,setLoading]=useState(true)
  const [courses,setCourses]=useState()
  const [courseId,setCourseId]=useState()
  const [teachers,setTeachers]=useState()
  const [teacherId,setTeacherId]=useState()
  const [time,setTime]=useState()
  const [date,setDate]=useState()
//   const name=(e)=>{
//     setBranchName(e.target.value)
//   }
  const navigate=useNavigate()
  const override={
    "display":"block",
    "margin":"3rem auto",

}
const {id}=useParams()
useEffect(()=>{
    apiServices.allBranch().then((data)=>{
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            // toast.success(data.data.Message)
            setCourses(data.data.Teacher)
        }
        else{
            toast.error(data.data.Message)
        }
    })
    apiServices.allTeacher().then((data)=>{
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            // toast.success(data.data.Message)
            setTeachers(data.data.Teacher)
        }
        else{
            toast.error(data.data.Message)
        }
    })
    let data={
        _id:id,
    }
    apiServices.singleBatch(data).then((data)=>{
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.Success){
            // toast.success(data.data.Message)
            setTeacherId(data.data.Batches?.teacher_id?._id)
            setCourseId(data.data.Batches.course_id._id)
            setTime(data.data.Batches.time)
            // setDate(data.data.Batches.starting_date)
            // console.log(data.data.Batches.starting_date)
            var date1=data.data.Batches.starting_date.toString().split('T')
            // console.log(date1[0])
            setDate(date1[0])
            // console.log("d is ", date1[0])
        }
        else{
            toast.error(data.data.Message)
        }
    })
},[])
const save=(e)=>{
   e.preventDefault()
   setLoading(true)
   let data={
    course_id:courseId,
    teacher_id:teacherId,
    time:time,
    starting_date:date,
    _id:id,
    status:true
   }
   apiServices.updateBatch(data).then((data)=>{
    setTimeout(()=>{
        setLoading(false)
    },1500)
    if(data.data.success){
        toast.success(data.data.message)
       setTimeout(()=>{
        navigate("/admin/view_batch")
       },1500)
    }
    else{
        toast.error(data.data.message)
    }
}).catch((error)=>{
    // console.log(error)
    toast.error("Something went wrong")
    setTimeout(()=>{
        setLoading(false)
    },1500)
})
}
const checkTime=()=>{
    // console.log("book ", date)
    const date1=new Date()
    const n_date=moment().format("YYYY-MM-DD",date1);
    // console.log(n_date)
    if(n_date>date){
        document.getElementById('time').value=""
        document.getElementById('error_time').innerHTML="Please Enter Valid Time"
    }
    else{
        document.getElementById('error_time').innerHTML=""
    }
  }
  return (
    <>
      <div className="container-fluid bg-primary py-5 mb-5 page-header">
        <div className="container py-5">
        <div className="row justify-content-center">
            <div className="col-lg-10 text-center">
            <h1 className="display-3 text-white animated slideInDown">Batches</h1>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                <li className="breadcrumb-item">
                    <Link className="text-white" to={"/admin"}>
                    Home
                    </Link>
                </li>
                <li
                    className="breadcrumb-item text-white active"
                    aria-current="page">
                    Batches
                </li>
                </ol>
            </nav>
            </div>
        </div>
        </div>
    </div>
    <div className="d-flex justify-content-center">
        <ClipLoader loading={loading} cssOverride={override} size={120}/>
    </div>
    <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}> 
        <div className="breadcrumbs " >
            <div className="container pt-2">
                <h2 className="text-center">Update Batches</h2>
            </div>
        </div>
        <div className='container'>
          <div className='row'>
            <div className='col-md-10 mx-auto my-3'>
            <form action='#'>
              <div className="form-group">
                <label htmlFor="name" className='form-label fs-4'>Course Name</label>
                <select className='form-control' onChange={(e)=>{setCourseId(e.target.value)}} value={courseId}>
                    <option  disabled value="">Choose Course</option>
                    {courses?.map((el,index)=>(
                        <option value={el?._id} selected={courseId==el?._id}>{el?.course_name}</option>
                    ))}
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="name" className='form-label fs-4'>Teacher</label>
                <select className='form-control' onChange={(e)=>{setTeacherId(e.target.value)}} value={teacherId}>
                    <option  disabled value="">Choose Teacher</option>
                    {teachers?.map((el,index)=>(
                        <option value={el?._id} selected={teacherId==el?._id}>{el?.teacher_name}</option>
                    ))}
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="name" className='form-label fs-4'>Time</label>
                <input type="time" className="form-control form-control-lg" id="name" value={time} onChange={(e)=>{setTime(e.target.value)}} placeholder="Batch Timing" />
              </div>
              <div className="form-group">
                <label htmlFor="name" className='form-label fs-4'>Date</label>
                <input type="date" className="form-control form-control-lg" value={date} onChange={(e)=>{setDate(e.target.value)}} placeholder="Batch Start Date" onBlur={checkTime} id="time"/>
                <span id="error_time" class="text-danger"></span>
              </div>
              <button type="submit" className="btn btn-primary my-3 col-4 offset-md-4 btn-lg" onClick={save}>Update</button>
            </form>
            </div>
          </div>
        </div>
    </div>
    </>
  )
}
