import React, { useEffect, useState } from 'react'
import {toast} from "react-toastify";
import { ClipLoader } from "react-spinners";
import apiServices from '../../services/apiServices';
import { Link, useParams } from 'react-router-dom';
import Moment from 'react-moment';
export default function AddAssignBatch() {
    const [loading,setLoading]=useState(true)
    const [courses,setCourses]=useState()
    const [courseId,setCourseId]=useState()
    const [batches,setBatches]=useState()
    const [batchId,setBatchId]=useState()
    const [student,setStudent]=useState()
    const {id}=useParams()
    // const name=(e)=>{
    //     setBranchName(e.target.value)
    // }
    const override={
        "display":"block",
        "margin":"3rem auto",
    }
    useEffect(()=>{
        let data={
            _id:id
        }
        apiServices.singleStudent(data).then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setStudent(data.data.Data)
            }
            else{
                toast.error(data.data.Message)
            }
        })
        apiServices.allBranch().then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setCourses(data.data.Teacher)
            }
            else{
                toast.error(data.data.Message)
            }
        })
        apiServices.allBatch().then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setBatches(data.data.Teacher)
            }
            else{
                toast.error(data.data.Message)
            }
        })
    },[])
    useEffect(()=>{
        if(courseId=="undefined"||courseId==null||courseId==undefined||courseId==""){
            var data={}
        }
        else{
        var data={
            course_id:courseId
        }
    }
        apiServices.allBatch(data).then((data)=>{
            setTimeout(()=>{
                setLoading(false)
            },1500)
            if(data.data.Success){
                // toast.success(data.data.Message)
                setBatches(data.data.Teacher)
            }
            else{
                toast.error(data.data.Message)
            }
        })
    },[courseId])
    const save=(e)=>{
    e.preventDefault()
    setLoading(true)
    let data={
        courseId:courseId,
        batchId:batchId,
        studentId:id
    }
    apiServices.addAssign(data).then((data)=>{
        setTimeout(()=>{
            setLoading(false)
        },1500)
        if(data.data.success){
            toast.success(data.data.message)
            setCourseId("")
           setBatchId("")
        }
        else{
            toast.error(data.data.message)
        }
    }).catch((error)=>{
        // console.log(error)
        // toast.error("Something went wrong")
        setTimeout(()=>{
            setLoading(false)
        },1500)
    })
    }
    return (
        <>
         <div className="container-fluid bg-primary py-5 mb-5 page-header">
        <div className="container py-5">
        <div className="row justify-content-center">
            <div className="col-lg-10 text-center">
            <h1 className="display-3 text-white animated slideInDown">Batch</h1>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb justify-content-center">
                <li className="breadcrumb-item">
                    <Link className="text-white" to={"/admin"}>
                    Home
                    </Link>
                </li>
                <li
                    className="breadcrumb-item text-white active"
                    aria-current="page">
                    Batch
                </li>
                </ol>
            </nav>
            </div>
        </div>
        </div>
    </div>
        <div className="d-flex justify-content-center">
            <ClipLoader loading={loading} cssOverride={override} size={60}/>
        </div>
        <div className={loading?"disabled-screen-full mymainbox":"mymainbox"}> 
        {/* <!-- ======= Breadcrumbs ======= --> data-aos="fade-in" */}
        <div className="breadcrumbs " >
                    <div className="container pt-2">
                        <h2 className='text-capitalize'>Assign Batch to {student?.student_name}</h2>
                    </div>
                    </div>
                    {/* <!-- End Breadcrumbs --> */}
            <div className='container'>
            <div className="d-flex justify-content-end mt-2">
                <Link to={"/admin/assign_class/"+id}>
                    <button className="btn btn-primary">View Assigned</button>
                </Link>
            </div>
            <div className='row'>
                <div className='col-md-10 mx-auto my-3'>
                <form action='#'>
                <div className="form-group">
                    <label htmlFor="name" className='form-label fs-4'>Course Name</label>
                    <select className='form-control' onChange={(e)=>{setCourseId(e.target.value)}} value={courseId}>
                        <option selected disabled value="">Choose Course</option>
                        {courses?.map((el,index)=>(
                            <option value={el?._id}>{el?.course_name}</option>
                        ))}
                    </select>
                </div>
                <div className="form-group">
                    <label htmlFor="name" className='form-label fs-4'>Batch</label>
                    <select className='form-control' onChange={(e)=>{setBatchId(e.target.value)}} value={batchId}>
                        <option selected disabled value="">Choose Batch</option>
                        {batches?.map((el,index)=>(
                            <option value={el?._id}><Moment format="YYYY/MM/DD">{el?.starting_date}</Moment>-{el?.time}</option>
                        ))}
                    </select>
                </div>
                <button type="submit" className="btn btn-primary my-3 col-4 offset-md-4 btn-lg" onClick={save}>ADD</button>
                </form>
                </div>
            </div>
            </div>
        </div>
        </>
    )
}
