import { Link, useNavigate } from "react-router-dom"
export default function Header() {
    const user_type=sessionStorage.getItem('user_type')
    const authenticate=sessionStorage.getItem('authenticate')
    const navigate=useNavigate()
    const logout=()=>{
        window.alert("Do you really Want To Logout?");
        sessionStorage.clear()
        setTimeout(()=>{
            sessionStorage.setItem("message","Logout Successfully")
            navigate("/login")
        },500)
    }
    return (
        <>
              {/* Navbar Start */}
  <nav className="navbar navbar-expand-lg bg-white navbar-light shadow sticky-top p-0">
    <Link
    to='/teacher'
      className="navbar-brand d-flex align-items-center px-4 px-lg-5"
    >
      <h2 className="m-0 text-primary">
        <i className="fa fa-book me-3" />
       Engg.HUB
      </h2>
    </Link>
    <button
      type="button"
      className="navbar-toggler me-4"
      data-bs-toggle="collapse"
      data-bs-target="#navbarCollapse"
    >
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarCollapse">
      <div className="navbar-nav ms-auto p-4 p-lg-0">
        <Link to='/teacher' className="nav-item nav-link">
          Home
        </Link>
        <div className="nav-item dropdown">
          <a
              href="#"
              className="nav-link dropdown-toggle"
              data-bs-toggle="dropdown"
          >
          Material
          </a>
          <div className="dropdown-menu fade-down m-0">
              <Link to="/teacher/add_material" className="dropdown-item">
              Add
              </Link>
              <Link to="/teacher/view_material" className="dropdown-item">
              Manage
              </Link>
          </div>
          </div>
          <div className="nav-item dropdown">
          <a
              href="#"
              className="nav-link dropdown-toggle"
              data-bs-toggle="dropdown"
          >
          PYQ'S
          </a>
          <div className="dropdown-menu fade-down m-0">
              <Link to="/teacher/addpaper" className="dropdown-item">
              Add
              </Link>
              <Link to="/teacher/viewpaper" className="dropdown-item">
              Manage
              </Link>
          </div>
          </div>  
        <div className="nav-item dropdown">
          <a
              href="#"
              className="nav-link dropdown-toggle"
              data-bs-toggle="dropdown"
          >
          Assignment
          </a>
          <div className="dropdown-menu fade-down m-0">
              <Link to="/teacher/add_assignment" className="dropdown-item">
              Add
              </Link>
              <Link to="/teacher/view_assignment" className="dropdown-item">
              Manage
              </Link>
          </div>
          </div> 
      </div>
      <a href="#" onClick={logout} className="btn btn-primary py-4 px-lg-5 d-none d-lg-block">
          Logout
          <i className="fa fa-arrow-right ms-3" />
      </a>
    </div>
  </nav>
  {/* Navbar End */}

        </>
    );
}